.loader{
    text-align:center;
    margin-top:20%;
}

.loader .loader__text{
    font-size: 50px;
}
.movies{
    display:table-cell;
}

.movies .movie{
    display:inline-flex ;
    background-color : white;
    margin : 40px;
    box-shadow: 7px 7px 13px 3px gray;
}
.movies .movie img {
    margin:10px 20px;
    position: relative;
    top:-35px;
    right:10px;
    box-shadow: 5px 5px 15px 5px #a29f9f;
}

.movie__data{
    margin:5px 20px;
    color:gray;
    width:300px;
}

.movies .movie .movie__title{
    font-size:24px;
    margin-top:40px;
    color:black;
}

.movies .movie .movie__year{
    font-size:20px;
}

.movies .movie .movie__genres{
    list-style : none;
    padding:0;
    display: flex;
}
.movies .movie .genres__genre{
  margin-right:4px;
}

.movies .movie .movie__summary{
    /* text-overflow: ellipsis;
    overflow: hidden;
    height: 55px; */
}

--img {border: 1px solid black}